@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;0,300;0,400;0,500;1,100;1,400&display=swap");

:root {
  --primary-color: #fff;
  --sub-primary-color: #f5f5f5;
  --secondary-color: #000;
  --sub-secondary-color: #d3d3d3;
  --hover-color: #ec1f55;
  --border-color: #170f23;
  --shadow-color: rgba(0, 0, 0, 0.19);
}
[data-theme="dark"] {
  --primary-color: #000;
  --sub-primary-color: #666666;
  --secondary-color: #fff;
  --sub-secondary-color: #f5f5f5;
  --hover-color: #ec1f55;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
}

html {
  font-size: 16px;
}

a {
  text-decoration: none;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
}
/* App */
.App {
  color: var(--secondary-color);
  background-color: var(--sub-primary-color);
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  gap: 50px;
  align-items: center;
  transition: all linear 0.5s;
}
/* Player body */

.music-player {
  position: relative;
  width: 350px;
  background: var(--primary-color);
  border-radius: 1.25rem;
  box-shadow: 0 10px 20px -5px var(--shadow-color);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
/* Artist Info */

.artist-info {
  padding-top: 1.5rem;
  padding-bottom: 1.25rem;
  text-align: center;
}

.details-title {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
  margin-left: 3rem;
  margin-right: 3rem;
}

.details-artist {
  font-weight: 400;
  color: #666;
}

/* Body cover */
.music-player--details {
  position: relative;
}
.details-img {
  height: 250px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.details-img img {
  object-fit: cover;
  width: 250px;
  height: 250px;
  border-radius: 50%;
}
.details-img.playing img {
  animation: playing 8s linear infinite alternate;
}
@keyframes playing {
  0% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(180deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
/* Progress bar */
.progress-bar {
  width: 90%;
  margin: 50px 5%;
}
.progress {
  width: 100%;
  -webkit-appearance: none;
  height: 8px;
  border-radius: 8px;
  background: var(--sub-secondary-color);
  outline: none;
  opacity: 0.7;
}

.progress::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 16px;
  height: 16px;
  border: 1px solid var(--secondary-color);
  border-radius: 50%;
  background-color: var(--primary-color);
  cursor: pointer;
}
/* Controls */

.music-player--controls {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin-bottom: 35px;
}

.music-player--controls .play-btn {
  padding: 1.6rem;
  margin-left: 2rem;
  margin-right: 2rem;
  font-size: 1.5rem;
  width: 73px;
  height: 73px;
  border: 1px solid var(--secondary-color);
  background: none;
  border-radius: 50%;
  box-shadow: 0 3px 6px var(--shadow-color);
  transition: all 0.4s;
  cursor: pointer;
  color: var(--secondary-color);
  display: flex;
  justify-content: center;
  align-items: center;
}

.play-btn:hover {
  transform: scale(1.1);
  color: var(--hover-color);
  opacity: 1;
  box-shadow: 0 6px 9px var(--shadow-color);
}

.music-player--controls .skip-btn {
  padding: 0.8rem;
  font-size: 1.2rem;
  border: none;
  outline: none;
  background: none;
  border-radius: 50%;
  box-shadow: 0 3px 6px var(--shadow-color);
  transition: all 0.3s;
  cursor: pointer;
  border: 1px solid var(--secondary-color);
  color: var(--secondary-color);
  display: flex;
  justify-content: center;
  align-items: center;
}

.skip-btn:hover {
  color: var(--hover-color);
  opacity: 1;
  box-shadow: 0 6px 9px var(--shadow-color);
}

/* Image */
.details-img--image {
  width: 25rem;
  height: 25rem;
}

/* Footer */

.list-icon {
  display: flex;
  margin: 0;
  padding: 0;
  list-style-type: none;
  justify-content: space-between;
  position: relative;
}

.icon {
  font-size: 1.5rem;
  transition: all 0.25s;
  cursor: pointer;
}
.icon.active {
  color: var(--hover-color);
}

.icon:hover {
  color: var(--hover-color);
}

.player__footer {
  padding-top: 1rem;
  padding-bottom: 2rem;
  padding-right: 2rem;
  padding-left: 2rem;
}

/* Volume bar */

.volume-bar {
  position: absolute;
  right: -14%;
  top: -200%;
  width: 100px;
  transform: rotate(-90deg);
  padding: 2px;
  display: none;
}
.volume-bar.active {
  display: flex;
}
.volume {
  width: 100%;
  -webkit-appearance: none;
  height: 8px;
  border-radius: 8px;
  background: var(--sub-secondary-color);
  opacity: 0.7;
  outline: none;
}

.volume::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 16px;
  height: 16px;
  border: 1px solid var(--secondary-color);
  border-radius: 50%;
  background-color: var(--primary-color);
  cursor: pointer;
}
/* Playlist songs */
.playlist {
  background-color: var(--sub-primary-color);
  height: 90%;
  width: 33rem;
  display: flex;
  flex-direction: column;
  border-radius: 1.25rem;
}
.playlist-details {
  background-color: var(--primary-color);
  padding-top: 1.5rem;
  padding-bottom: 1.25rem;
  font-size: 1.6rem;
  font-weight: 800;
  box-shadow: 0 2px 3px var(--shadow-color);
  border-radius: 1.25rem;
  margin: 0.5rem 0.3rem;
  text-transform: uppercase;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.playlist-type {
  width: 50%;
  cursor: pointer;
}
.playlist-type.active {
  color: var(--hover-color);
}
.playlist-type-playlist {
  border-right: 1px solid var(--secondary-color);
}
.playlist-list {
  list-style-type: none;
  display: flex;
  flex-direction: column;
  overflow: auto;
  padding: 1rem;
  background-color: var(--sub-primary-color);
  margin-right: 1rem;
}
.playlist-list::-webkit-scrollbar {
  width: 0.8rem;
}
.playlist-list::-webkit-scrollbar-thumb {
  background: var(--secondary-color);
  border-radius: 10px;
}

.playlist-song {
  color: var(--secondary-color);
  background-color: var(--primary-color);
  display: flex;
  border-radius: 1.25rem;
  margin-bottom: 1.2rem;
  align-items: center;
  box-shadow: 0 10px 20px -5px var(--shadow-color);
  cursor: pointer;
}

.playlist-song.playing {
  background-color: var(--hover-color);
}
.playlist-song.playing > .playlist-song-details > .playlist-song-artist {
  color: var(--secondary-color);
}
.playlist-song-img {
  width: 85px;
  height: 85px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.playlist-song-img > img {
  width: 70%;
  height: 70%;
  border-radius: 50%;
  object-fit: cover;
}
.playlist-song-details {
  width: 60%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  text-align: start;
}
.playlist-song-title {
  font-size: 1.3rem;
  font-weight: 600;
  text-align: left;
}
.playlist-song-artist {
  font-size: 0.9rem;
  opacity: 0.7;
}
.playlist-song-icon {
  width: 20%;
}
.playlist-song.playing > .playlist-song-icon:hover {
  color: rgb(238, 255, 0);
}
/* Theme Switcher */
.theme-container {
  background: var(--primary-color);
  position: absolute;
  right: 0;
  top: 2.7rem;
  padding: 1rem;
  width: fit-content;
  border: 1px solid var(--secondary-color);
  height: 100px;
  display: none;
}
.theme-container.active {
  display: block;
}
.theme-container .theme-title {
  font-weight: 600;
  text-transform: uppercase;
  margin-bottom: 1rem;
}
.open-theme-btn {
  position: absolute;
  right: 0;
  top: 0;
  padding: 0.5rem;
  background-color: var(--primary-color);
  border: 1px solid var(--secondary-color);
}
.theme-btn-container {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.theme > input[type="radio"] {
  transform: translateY(1.5rem);
}
.theme-light {
  width: 25px;
  height: 25px;
  border: 1px solid black;
  border-radius: 50%;
  background-color: #fff;
}
.theme-dark {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  border: 1px solid #fff;
  background-color: #000;
}
/* Responsive */
@media screen and (max-width: 768px) and (min-width: 576px) {
  .App {
    flex-direction: column;
    height: auto;
  }
  .music-player {
    margin-top: 3rem;
    width: 70%;
  }
  .volume-bar {
    right: -8.3%;
  }
  .playlist {
    width: 33rem;
  }
}
@media screen and (max-width: 576px) {
  .App {
    flex-direction: column;
    height: auto;
  }
  .music-player {
    margin-top: 3rem;
  }
  .playlist {
    width: 95%;
  }
}
